<!--  DOM  -->
<template>
    <div class="assess">
        <ul>
            <li v-for="(o,i) of assessList" :key="i">
                <span :class="{'active':o.class}" @click="tapFn(i)">{{o.name}}</span>
            </li>
        </ul>
        <div class="btn">
            <button class="canle" @click="canleFn"></button>
            <button class="sure" @click="sureFn"></button>
        </div>
    </div>
</template>
<script>
export default {
    name: 'assess', // 
    data() {
        return {
            // 
            assessList:[
                {
                    name:'2',
                    class:false,
                },{
                    name:'1',
                    class:false,
                },{
                    name:'',
                    class:false,
                },{
                    name:'',
                    class:false,
                },{
                    name:'',
                    class:false,
                },{
                    name:'',
                    class:false,
                },{
                    name:'',
                    class:false,
                },{
                    name:'',
                    class:false,
                },{
                    name:'',
                    class:false,
                }
            ],
            selectPush:[],
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        tapFn(i){
            let that=this;
            if(!that.assessList[i].class){
                that.selectPush.push(that.assessList[i].name);
                that.assessList[i].class=true;
            }else{
                for(let x in that.selectPush){
                    if(that.selectPush[x]==that.assessList[i].name){
                        that.selectPush.splice(x,1)
                    }
                }
                that.assessList[i].class=false;
            }
            
        },
        sureFn(){
            let str=this.selectPush.join('||');
            this.$parent.mask=false;
            this.$emit('sureFn',str);
        },
        canleFn(){
            this.$parent.mask=false;
            this.$emit('canleFn',str);
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.assess{
    position: absolute;
    height: 705px;
    background: #f5f5f5;
    bottom: -705px;
    left: 0;
    z-index: 9;
    width: 100%;
    padding:50px 0;
    box-sizing: border-box;
    ul{
        display: flex;
        flex-wrap: wrap;
        padding: 0 32px;
        margin-bottom: 150px;
        li{
            text-align: center;
            margin-bottom: 32px;
            // width: 50%;
            span{
                margin-right: 60px;
                padding: 15px 40px;
                display: inline-block;
                border-radius:10px;
                text-align: center;
                color: #999;
                border: 1PX solid #CCBEBE
            }
            .active{
                border-color: #FE6603;
                background: #FE6603;
                color: #fff;
            }
        }
    }
    .btn{
        display: flex;
        button{
            height: 84px;
            flex-grow:1;
        }
        .sure{
            background: #FE6603;
            border: 1PX solid #FE6603;
            color: #fff;
        }
        .canle{
            border: 1PX solid #CCBEBE;
            color: #333;
            background: #fff;
        }
    }
    
}
</style>



